import { _decorator, Color, Component, Label, Node, Sprite, Tween, tween, Animation } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('CorrectLabelComponent')
export class CorrectLabelComponent extends Component {

    //private readonly possibleLabelText = ['Отлично!', 'Хорошо!', 'Прекрасно!'];
    private readonly possibleLabelText = ['Nice!', 'Awesome!', 'Perfect!'];

    private readonly showClipName = "showCorrect";

    @property({type: Sprite})
    private sprite = null;

    @property({type:Label})
    private label = null;

    @property({type:Animation})
    private animation = null;

    start() {
        this.sprite.color = this.getColorWithZeroA(this.sprite.color);
    }

    show() : Tween<Node> {

        this.label.string = this.possibleLabelText[Math.floor(Math.random() * this.possibleLabelText.length)];

        this.animation.play(this.showClipName);
        
        return tween(this.node)
            .delay(this.animation.getState(this.showClipName).clip.duration);
    }

    private getColorWithZeroA(color: Color) : Color {
        color.a = 0;
        return color;
    }
}
